/* ImageDetail.css */
.image-detail {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    z-index: 1000;
    
  }
  
  .title{
    margin-top: 100px;
    text-align: center;
    font-size: calc(10px + 2vmin);
  }

  .description{
    text-align: center;
    font-size: calc(2vmin);
  }

  .tilt-image-container {
    perspective: 1000px; /* Adds depth to the 3D effect */
  }
  
  .full-image {
    margin-top:100px;
    width: 60vw; /* Make the image responsive to the viewport width */
    object-fit: contain; /* Maintain aspect ratio */
    max-height: 70vh; /* Prevent the image from exceeding viewport height */
    max-height: 590px;
    transition: transform 0.1s ease-out; /* Smooth transition */
    transform-style: preserve-3d;
    
  }
  
  .back-button {
    position: absolute;
    top: 20px;
    left: 20px;
    padding: 10px 20px;
    font-size: calc(2vmin);
    cursor: pointer;
    color: #ffffff;
    background: transparent;
    border: white solid 1px;
    border-radius: 5px;
  }